interface PortfolioHeaderProps {
    activeId: string;
    setActive: (activeId) => void;
  }
  
 export const PortfolioHeader = (headerProps: PortfolioHeaderProps) => {
    const navigationItems = [
      { name: "DRAWINGS",  },
      { name: "PAINTING",  },
      { name: "DESIGN",  },
      { name: "PHOTOGRAPHY",  },
    ];
  
    const subHeader = (activeId: string, setActive: (id: string) => void) => {
      return navigationItems.map((item) => {
        return (
          <div
            onClick={() => {
              setActive(item.name);
            }}
            style={
              activeId === item.name
                ? { marginRight: "1rem", borderBottom: "1px solid white" }
                : { marginRight: "1rem", borderBottom: "1px solid rgba(18, 18, 18, .9"}
            }
            className={`sub-header${activeId === item.name ? "-visited" : ""}`}
          >
            {item.name}
          </div>
        );
      });
    };
  
    return <div style = {{        display: "flex",
    alignItems: "center",
    flexDirection: "row",}}>{subHeader(headerProps.activeId, headerProps.setActive)}</div>;
  };
  