import {
  listAll,
  StorageReference,
  ref,
  getDownloadURL,
  FirebaseStorage,
  list,
  ListResult,
} from "firebase/storage";
import React from "react";
import { useEffect } from "react";
import { PortfolioHeader } from "../../components/HeaderPortfolio";
import CloseIcon from "@material-ui/icons/Close";

interface PortfolioProps {
  refProp: any;
  storage: FirebaseStorage;
}
interface LatestImagesProps {
  urlMap: Record<string, string>;
  setOverlay: (val) => void;
}
const LatestImages = (props: LatestImagesProps) => {
  return (
    <div style={{ marginTop: "1.5rem" }}>
      <div>
        <div className="row">
          <div className="col ">
            <img
              id={"img0"}
              className={"picture "}
              onClick={() => {
                const img = document.getElementById(`overlay`);
                img?.setAttribute("src", props.urlMap["img0"]);
                props.setOverlay(true);
              }}
            />
          </div>
          <div className="col hoverImage">
            <img
              id={"img1"}
              className={"picture"}
              onClick={() => {
                const img = document.getElementById(`overlay`);
                img?.setAttribute("src", props.urlMap["img1"]);
                props.setOverlay(true);
              }}
            />
          </div>
          <div className="col hoverImage">
            <img
              id={"img2"}
              className={"picture"}
              onClick={() => {
                const img = document.getElementById(`overlay`);
                
                img?.setAttribute("src", props.urlMap["img2"]);
                props.setOverlay(true);
              }}
            />
          </div>
        </div>
        <div className="row" style={{ marginTop: "1.5rem" }}>
          <div className="col hoverImage">
            <img
              id={"img3"}
              className={"picture"}
              onClick={() => {
                const img = document.getElementById(`overlay`);
                img?.setAttribute("src", props.urlMap["img3"]);
                props.setOverlay(true);
              }}
            />
          </div>
          <div className="col hoverImage">
            <img
              id={"img4"}
              className={"picture"}
              onClick={() => {
                const img = document.getElementById(`overlay`);
                img?.setAttribute("src", props.urlMap["img4"]);
                props.setOverlay(true);
              }}
            />
          </div>
          <div className="col hoverImage">
            <img
              id={"img5"}
              className={"picture"}
              onClick={() => {
                const img = document.getElementById(`overlay`);
                img?.setAttribute("src", props.urlMap["img5"]);
                props.setOverlay(true);
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

const Portfolio = (props: PortfolioProps) => {
  const [portfolioId, setPorfolio] = React.useState("DRAWINGS");
  const [overlay, setOverlay] = React.useState(false);
  const [urlMap, setUrlMap] = React.useState({});
  const [pageIndex, setPageIndex] = React.useState(0);
  const [images, setImages] = React.useState<ListResult>();

  const populateImages = (list) => {
    const tempUrlMap = {...urlMap};
    list.slice(pageIndex, pageIndex + 6).forEach((itemRef, index) => {
      getDownloadURL(ref(props.storage, itemRef.fullPath)).then((url) => {
        const img = document.getElementById(`img${index}`);
        tempUrlMap[`img${index}`] = url;
        img?.setAttribute("src", url);
      });
    });
    setUrlMap(tempUrlMap);
  }

  useEffect(() => {
    const listRef = ref(props.storage, portfolioId);
    for (let i = 0; i < 6; i++){
      const img = document.getElementById(`img${i}`);
      img?.setAttribute("src", "https://wallpapercave.com/wp/wp4946969.jpg");
      img?.setAttribute("type", "non-click");
    }
    if (images){
      populateImages(images.items);
    }
    else{
      list(
        listRef,
         { maxResults: 100 }
      ).then((res) => {
        populateImages(res.items);
        setImages(res);
      });
    }

  }, [pageIndex, portfolioId]);

  return (
    <div ref={props.refProp} style={{ marginBottom: "3rem" }}>
      <div
        className={"overlay"}
        style={{
          display: overlay ? "block" : "none",
        }}
      >
        <div className="overlayImage">
          <div className={"closeIcon"} onClick={() => setOverlay(false)}>
            <CloseIcon />
          </div>
          <img className={"picture"} id={"overlay"} />
        </div>
      </div>
      <h2 style={{ color: "white" }}>WORK</h2>

      <PortfolioHeader activeId={portfolioId} setActive={(activeId) => 
      {
        setPorfolio(activeId);
        setImages(undefined);
        setPageIndex(0);
      }} />
      <LatestImages urlMap={urlMap} setOverlay={setOverlay} />
      <div
        style={{
          marginBottom: "4rem",
          float: "right",
          marginTop: ".5rem",
          display: "flex",
        }}
      >
        {pageIndex > 0 && <div
          style={{
            color: "white",
            cursor: "pointer",
          }}
          onClick={() => {
            setPageIndex(pageIndex - 6);
          }}
        >
          Previous
        </div>}

        {images && (pageIndex + 6) <= images.items.length && <div
          style={{
            color: "white",
            cursor: "pointer",
            marginLeft: '1rem'

          }}
          onClick={() => {
            setPageIndex(pageIndex + 6);
          }}
        >
          Next
        </div>}
      </div>
    </div>
  );
};

export default Portfolio;
