import "../main.scss";

interface HeaderProps {
  activeId: string;
  setActive: React.Dispatch<React.SetStateAction<string>>;
  portfolioRef: React.MutableRefObject<null>;
  homeRef: React.MutableRefObject<null>;
  executeScroll: (ref: React.MutableRefObject<null>) => void;
}

const Header = (headerProps: HeaderProps) => {
  const navigationItems = [
    { name: "HOME", ref: headerProps.homeRef },
    { name: "WORK", ref: headerProps.portfolioRef },
  ];

  const subHeader = (activeId: string, setActive: (id: string) => void) => {
    return navigationItems.map((item) => {
      return (
        <div
          onClick={() => {
            setActive(item.name);
            headerProps.executeScroll(item.ref);
          }}
          style={{ marginLeft: "1rem" }}
          className={`sub-header${activeId === item.name ? "-visited" : ""}`}
        >
          {item.name}
        </div>
      );
    });
  };

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        position: "sticky",
        top: 0,
        backgroundColor: "rgba(18, 18, 18, .9)",
        borderBottom: "1px solid rgba(255, 255, 255, 0.12)",
        backdropFilter: "blur(5px)"
      }}
    >
      <div
        style={{ marginTop: "1rem" }}
        className="main-header"
      >
        
        JOSEPH SAUTO
      </div>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          flexDirection: "row",
          marginRight: "1rem",
          marginTop: ".25rem",
          marginBottom: "1rem",
        }}
      >
        {subHeader(headerProps.activeId, headerProps.setActive)}
      </div>
    </div>
  );
};

export default Header;
